<template>
  <div>
    <!-- Navbar with "Back" and "Edit" buttons -->
    <div class="navbar">
      <button class="btn btn-close" @click="goBack">Back</button>
      <h2 class="title">Character Details</h2>
      <button v-if="!isEditable" class="btn btn-edit" @click="toggleEdit">Edit</button>
    </div>

    <!-- Character Details Card -->
    <div class="details-container card">
      <!-- Character Image -->
      <div class="character-image-details">
        <img :src="character.path" alt="Character Image" />
      </div>

      <!-- Character Information -->
      <div class="character-info">
        <div class="form-group">
          <label for="name">Name:</label>
          <input id="name" v-model="character.name" :disabled="!isEditable" />
        </div>

        <div class="form-group">
          <label for="path">Path:</label>
          <input id="path" v-model="character.path" :disabled="!isEditable" />
        </div>

        <div class="form-group">
          <label for="htmlCode">HTML Code:</label>
          <div class="html-code-container">
            <textarea id="htmlCode" v-model="character.htmlCode" :disabled="!isEditable"></textarea>
            <div class="copy-overlay-details" v-if="copied">Copied!</div>
          </div>
          <button class="btn btn-copy" @click="copyHTMLCode">Copy HTML Code</button>
        </div>

        <div class="form-group">
          <label for="tags">Tags:</label>
          <input id="tags" v-model="tagsInput" :disabled="!isEditable" placeholder="Enter tags, separated by commas" />
        </div>

        <!-- Action Buttons (Visible only in Edit Mode) -->
        <div class="action-buttons" v-if="isEditable">
          <div class="left-actions">
            <button class="btn btn-delete" @click="confirmDelete">Delete</button>
          </div>
          <div class="right-actions">
            <button class="btn btn-cancel" @click="cancelEdit">Cancel</button>
            <button class="btn btn-save" @click="saveDetails">Save</button>
          </div>
        </div>
      </div>
    </div>

    <!-- Delete Confirmation Dialog -->
    <div v-if="showDeleteConfirmation" class="delete-confirmation-overlay">
      <div class="delete-confirmation-box">
        <p>Are you sure you want to delete this character?</p>
        <button class="btn btn-yes" @click="deleteCharacter">Yes</button>
        <button class="btn btn-no" @click="closeDeleteConfirmation">No</button>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, get, update, remove } from 'firebase/database';
import { db } from '../firebase';

export default {
  data() {
    return {
      character: {
        name: '',
        path: '',
        htmlCode: '',
        tags: [],
      },
      isEditable: false,
      tagsInput: '',
      copied: false, // State to handle copy overlay
      showDeleteConfirmation: false, // Show delete confirmation dialog
    };
  },
  methods: {
    async fetchCharacterDetails() {
      const characterId = this.$route.params.id;
      const characterRef = ref(db, `characters/${characterId}`);

      try {
        const snapshot = await get(characterRef);
        if (snapshot.exists()) {
          this.character = snapshot.val();
          this.tagsInput = this.character.tags.join(', ');
        } else {
          console.error('Character not found');
        }
      } catch (error) {
        console.error('Error fetching character details:', error);
      }
    },
    toggleEdit() {
      this.isEditable = !this.isEditable;
    },
    cancelEdit() {
      this.isEditable = false;
      this.fetchCharacterDetails(); // Re-fetch details to cancel changes
    },
    async saveDetails() {
      const characterId = this.$route.params.id;
      this.character.tags = this.tagsInput.split(',').map(tag => tag.trim());

      const characterRef = ref(db, `characters/${characterId}`);
      try {
        await update(characterRef, this.character);
        this.isEditable = false;
      } catch (error) {
        console.error('Error saving character details:', error);
      }
    },
    copyHTMLCode() {
      navigator.clipboard.writeText(this.character.htmlCode)
        .then(() => {
          this.copied = true;
          setTimeout(() => {
            this.copied = false; // Hide the overlay after 2 seconds
          }, 2000);
        })
        .catch(err => console.error('Error copying HTML Code:', err));
    },
    goBack() {
      this.$router.push({ name: 'CharacterList' });
    },
    confirmDelete() {
      this.showDeleteConfirmation = true;
    },
    closeDeleteConfirmation() {
      this.showDeleteConfirmation = false;
    },
    async deleteCharacter() {
      const characterId = this.$route.params.id;
      const characterRef = ref(db, `characters/${characterId}`);

      try {
        await remove(characterRef);
        this.$router.push({ name: 'CharacterList' }); // Navigate back after deletion
      } catch (error) {
        console.error('Error deleting character:', error);
      }
    },
  },
  mounted() {
    this.fetchCharacterDetails();
  },
};
</script>

